<template>
    <Dictionary
        v-if="data.ready"
        :code="props.code"
        :type="data.type"
    /> 

    <Ready page="mood" :init="init" />
</template>

<script setup>
    import { onMounted, onBeforeUnmount, reactive } from 'vue';
    import Dictionary from './components/Dictionary.vue';
    import Ready from 'ðŸ”—/components/ui/Ready.vue';
    import api from 'ðŸ”—/api';
    import stores from '@/stores';

    const dict = api.dict();
    const locale = stores.locale();


    const props = defineProps({
        code: { type: String }
    });

    const data = reactive({
        ready: false,
        type: null
    });

    const init = async () => {
        const types = await dict.getTypes();

        data.type = types.find(item => item.code === 'genres');
        data.ready = true;
    }
</script>
